/**
 * Draws the button to toggle the display year.
 *
 * @param {*} data The d3 Selection of the graph's g SVG element
 * @param {*} title The d3 Selection of the graph's g SVG element
 */
export function drawButton (data, title) {
  const buttonHeight = 30
  const groupWidth = getWidth()
  const positionArray = getXPositionArray(data, groupWidth)

  // Title
  d3.select('.filter').append('text').text(title).attr('font-size', '14px')

  // Groups
  const buttonGroup = d3.select('.filter')
    .append('svg')
    .attr('class', 'section')
    .attr('width', groupWidth)
    .attr('height', buttonHeight * 2)
    .selectAll('svg')
    .data(data)
    .enter()
    .append('svg')
    .attr('id', d => 'button-svg-' + d)

  // Rectangles
  buttonGroup.append('rect')
    .attr('class', 'button')
    .attr('id', d => 'button-rect-' + d)
    .attr('width', d => positionArray.filter((p) => p.Key === d)[0].Width * 0.98)
    .attr('height', buttonHeight)
    .attr('x', d => positionArray.filter((p) => p.Key === d)[0].Position)
    .attr('y', buttonHeight * 0.5)
    .attr('fill', '#33333')
    .attr('rx', buttonHeight / 2)
    .on('mouseenter', function () { d3.select(this).attr('opacity', 0.8) })
    .on('mouseleave', function () { d3.select(this).attr('opacity', 1) })
    .on('click', function () {
      d3.select(this).attr('fill', d => d3.select(this).attr('fill') === '#33333' ? '#EAEAEA' : '#33333')
      d3.select(this.parentNode).selectAll('text').attr('fill', d => d3.select(this.parentNode).selectAll('text').attr('fill') === '#33333' ? '#EAEAEA' : '#33333')
    })

  // Text
  buttonGroup.append('text')
    .attr('id', d => 'button-text-' + d)
    .attr('text-anchor', 'middle')
    .text(d => d)
    .attr('x', function (d) {
      return positionArray.filter((p) => p.Key === d)[0].Position + (positionArray.filter((p) => p.Key === d)[0].Width) / 2
    })
    .attr('y', buttonHeight * 1.2)
    .attr('font-size', '14px')
    .attr('fill', '#fff')
    .attr('pointer-events', 'none')
}

/**
 * Draws the button to toggle the display year.
 *
 * @returns {number} dimensions
 */
export function getWidth () {
  const parentNode = d3.select('.filter').node()
  const bounds = parentNode.getBoundingClientRect()
  const padding = {
    left: parseFloat(window.getComputedStyle(parentNode).paddingLeft),
    right: parseFloat(window.getComputedStyle(parentNode).paddingRight)
  }
  return bounds.width - padding.left - padding.right
}

/**
 * Draws the button to toggle the display year.
 *
 * @param {*} data The d3 Selection of the graph's g SVG element
 * @param {*} width The d3 Selection of the graph's g SVG element
 * @returns {object[]} scale
 */
export function getXPositionArray (data, width) {
  const totalLength = data.map((d) => d.toString().length).reduce((a, b) => a + b)
  const dataArray = []
  data.forEach((d) => dataArray.push(
    {
      // Index: dataArray.length,
      Key: d,
      // Length: d.toString().length,
      Width: d.toString().length / totalLength * width,
      Position: (dataArray.length === 0 ? 0 : d.toString().length / totalLength * width)
    }
  ))
  dataArray.map((d, index, self) => {
    if (index === 0) {
      d.Position = 0
    } else {
      d.Position = self[index - 1].Position + self[index - 1].Width
    }
  })
  return dataArray
}
