/**
 * Defines the color scale used in the graph.
 *
 * @returns {*} The color scale to be used inthe graph
 */
export function defineColorScaleGravity () {
  const graviteColors = [
    { Gravite: 'Dommages materiels inferieurs au seuil de rapportage', Color: '#34C6BB' }, // Green
    { Gravite: 'Dommages materiels seulement', Color: '#99E3DD' }, // Lighter green
    { Gravite: 'Leger', Color: '#F2C80F' }, // Yellow
    { Gravite: 'Grave', Color: '#FE9666' }, // Orange
    { Gravite: 'Mortel', Color: '#FD625E' } // Red
  ]
  const gravite = graviteColors.map(function (arr) { return arr.Gravite })
  const colors = graviteColors.map(function (arr) { return arr.Color })

  return d3.scaleOrdinal().range(colors).domain(gravite)
}

/**
 * Defines the color scale used in the graph.
 *
 * @param {object[]} data The accident data
 * @returns {*} The color scale to be used inthe graph
 */
export function getDistinctYear (data) {
  return [...new Set(data.map(i => i.Year))]
}

/**
 * Defines the color scale used in the graph.
 *
 * @param {object[]} data The accident data
 * @returns {*} The color scale to be used inthe graph
 */
export function getDistinctGravite (data) {
  return [...new Set(data.map(i => i.GRAVITE))]
}
