/**
 * Displays the markers for each street on the map.
 *
 * @param {object[]} data The street data to be displayed
 */
export function drawCard (data) {
  const insideMargin = 10
  const cardDimension = getCardSideDimension(insideMargin)
  document.querySelectorAll('.cardcontainer')[0].style.height = cardDimension * 2.1

  drawPercentageCard(getTotalCountAccident(data), [0, 0], cardDimension, insideMargin)
  drawPercentageCard(getYearlyAverage(data), [1, 0], cardDimension, insideMargin)
  drawPercentageCard(getBestYear(data), [0, 1], cardDimension, insideMargin)
  drawPercentageCard(getWorstYear(data), [1, 1], cardDimension, insideMargin)

  /**
   * Sets up the click handler for the button.
   *
   * @param {object[]} data Accident data
   * @param {object[]} position card position
   * @param {object[]} cardDimension card position
   * @param {object[]} insideMargin card position
   */
  function drawPercentageCard (data, position, cardDimension, insideMargin) {
    // Group
    const element = d3.selectAll('#Card')
      .append('g')
      .attr('id', 'Card-g-' + position[0].toString() + position[1].toString())

    // Rectangle
    element.append('rect')
      .attr('x', (cardDimension + insideMargin) * position[0])
      .attr('y', (cardDimension + insideMargin) * position[1])
      .attr('width', cardDimension)
      .attr('height', cardDimension)
      .attr('fill', '#f5f9fd')
      .attr('rx', '15')

    // KPI Value
    element.append('text')
      // .attr('class', 'cardValue')
      .text(data.Value)
      .attr('x', (cardDimension + insideMargin) * position[0])
      .attr('y', (cardDimension + insideMargin) * position[1])
      .attr('transform', `translate(${cardDimension / 2},${cardDimension / 2})`)
      .attr('text-anchor', 'middle')
      .attr('font-size', 36)
      .attr('font-weight', 'bold')

    // Text Value
    element.append('text')
      // .attr('class', 'cardDetail')
      .attr('x', (cardDimension + insideMargin) * position[0])
      .attr('y', (cardDimension + insideMargin) * position[1])
      .attr('transform', `translate(${cardDimension / 2},${cardDimension * 0.75})`)
      .attr('text-anchor', 'middle')
      .attr('font-size', 12)
      .attr('font-weight', 'regular')
      .text(data.Message)
      .attr('dy', '0em')

    // 2nd Text Value
    element.append('text')
      // .attr('class', 'cardDetail')
      .attr('x', (cardDimension + insideMargin) * position[0])
      .attr('y', (cardDimension + insideMargin) * position[1])
      .attr('transform', `translate(${cardDimension / 2},${cardDimension * 0.75})`)
      .attr('text-anchor', 'middle')
      .attr('font-size', 12)
      .attr('font-weight', 'regular')
      .text(data.Message2)
      .attr('dy', '1.5em')
  }
}

/**
 * Draws the button to toggle the display year.
 *
 * @param {number} margin inside margin
 * @returns {number} dimensions
 */
export function getCardSideDimension (margin) {
  const parentNode = d3.select('#Card').node()
  const totalWidth = parentNode.getBoundingClientRect().width
  return (totalWidth - margin) / 2
}

/**
 * Draws the button to toggle the display year.
 *
 * @param {object[]} data inside margin
 * @returns {object[]} dimensions
 */
export function getTotalCountAccident (data) {
  const returnArray = {
    Value: data.length.toLocaleString('fr-CA'),
    Message: 'Total d\'accidents'
  }
  return returnArray
}

/**
 * Draws the button to toggle the display year.
 *
 * @param {object[]} data inside margin
 * @returns {object[]} dimensions
 */
export function getWorstYear (data) {
  // Group by
  const yearList = data.map(function (arr) { return arr.Year })
  let yearCount = yearList.reduce((a, c) => (a[c] = (a[c] || 0) + 1, a), {})
  yearCount = Object.keys(yearCount).map(key => ({ Year: key, Count: yearCount[key]})).sort((a, b) => b.Count - a.Count)

  const returnArray = {
    Value: yearCount[0].Year,
    Message: 'Pire année,',
    Message2: `avec ${yearCount[0].Count.toLocaleString('fr-CA')} accidents`
  }
  return returnArray
}

/**
 * Draws the button to toggle the display year.
 *
 * @param {object[]} data inside margin
 * @returns {object[]} dimensions
 */
export function getBestYear (data) {
  // Group by
  const yearList = data.map(function (arr) { return arr.Year })
  let yearCount = yearList.reduce((a, c) => (a[c] = (a[c] || 0) + 1, a), {})
  yearCount = Object.keys(yearCount).map(key => ({ Year: key, Count: yearCount[key]})).sort((a, b) => a.Count - b.Count)

  const returnArray = {
    Value: yearCount[0].Year,
    Message: 'Meilleure année,',
    Message2: `avec ${yearCount[0].Count.toLocaleString('fr-CA')} accidents`
  }
  return returnArray
}

/**
 * Draws the button to toggle the display year.
 *
 * @param {object[]} data inside margin
 * @returns {object[]} dimensions
 */
export function getYearlyAverage (data) {
  const yearCount = [...new Set(data.map(i => i.Year))].length
  const accidentCount = data.length
  const returnArray = {
    Value: Math.round(accidentCount / yearCount).toLocaleString('fr-CA'),
    Message: 'Moyenne par année'
  }
  return returnArray
}
