'use strict'

import * as preproc from './scripts/preprocess.js'
// import * as helper from './scripts/helper.js'
// import * as viz from './scripts/viz.js'
import * as button from './scripts/buttons.js'
import * as map from './scripts/visuals/Map.js'
import * as dotmap from './scripts/visuals/DotMap.js'
import * as percchart from './scripts/visuals/PercentageChart.js'
import * as card from './scripts/visuals/Card.js'


/**
 * @file This file is the entry-point for the the code our final project for the course INF8808E.
 * @author Mamadou Saliou Diallo, Maxim Laurin, Nina Cussa
 * @version v1.0.0
 */

(function (d3) {
  build()

  /**
   *   This function builds the graph.
   */
  function build () {
    var projection = map.getProjection()
    var path = map.getPath(projection)

    // var borough
    d3.json('./montreal.json').then(function (borough) {
      borough = preproc.reverseGeoJsonCoordinates(borough)
      // map.drawMap(borough, path)

      d3.csv('./collisions_routieres (cleaned).csv', function (d) {
        return {
          NO_SEQ_COLL: d.NO_SEQ_COLL,
          DT_ACCDN: d3.timeParse('%Y-%m-%d')(d.DT_ACCDN),
          HEURE_ACCDN: d.HEURE_ACCDN,
          GRAVITE: d.GRAVITE,
          LOC_LONG: +d.LOC_LONG,
          LOC_LAT: +d.LOC_LAT
        }
      }).then(function (data) {
        // Process the data
        preproc.addDateAttributes(data)
        data = preproc.replaceValues(data)
        preproc.setCoordinates(data, projection)
        // Get distinct values from specific fields
        const distinctYear = preproc.getDistinctYear(data)
        const distinctGravite = preproc.getDistinctGravite(data)
        const distinctFilters = distinctYear.concat(distinctGravite)
        // preproc.addBoroughs(data, borough)
        // Draw visuals
        buildVisuals(data)
        button.drawButton(distinctYear, 'Années')
        button.drawButton(distinctGravite, 'Niveau de gravité')
        setClickHandler()
        /**
         * Sets up the click handler for the button.
         *
         * @param {object[]} data Accident data
         *
         */
        function buildVisuals (data) {
          const colorGravite = preproc.defineColorScaleGravity()
          card.drawCard(data)
          percchart.drawPercentageChart(data, colorGravite)
        }

        /**
         *   Sets up the click handler for the button.
         */
        function setClickHandler () {
          d3.selectAll('.button')
            .on('click', (event, d) => {
              if (!(distinctFilters.length === 1 && distinctFilters[0] === d)) { // Can't unselect them all
                if (!distinctFilters.includes(d)) {
                  distinctFilters.push(d)
                } else {
                  distinctFilters.splice(distinctFilters.indexOf(d), 1)
                }

                // Change color
                const buttonRect = d3.select(`rect[id='button-rect-${d}']`); buttonRect.attr('fill', d => buttonRect.attr('fill') === '#33333' ? '#EAEAEA' : '#33333')
                const buttonText = d3.select(`text[id='button-text-${d}']`); buttonText.attr('fill', d => buttonText.attr('fill') === '#fff' ? '#33333' : '#fff')

                // Rebuild visuals
                const dataFiltered = data.filter(row => distinctFilters.includes(row.Year) && distinctFilters.includes(row.GRAVITE))
                buildVisuals(dataFiltered)
              }
            })
        }
      })
    })
  }
})(d3)
