/**
 * Displays the markers for each street on the map.
 *
 * @param {object[]} data The street data to be displayed
 * @param {*} colorScale The color scaled used to determine the color of the circles
 */
export function drawPercentageChart (data, colorScale) {
  document.querySelectorAll('.percentagechart')[0].style.height = d3.select('#Card').node().getBoundingClientRect().height - 10
  const dimension = getDimensions()

  const margin = { top: 65, right: 50, bottom: 35, left: 155 }
  const graphSize = dimension // setSizing(margin)

  // Preprocess the data
  const graviteList = data.map(function (arr) { return arr.GRAVITE }) // Keep the Gravity column only
  const rowCount = graviteList.length
  const graviteCount = getDistinctCount(graviteList, rowCount)

  // Prepare the visual
  const xScale = defineXScale(rowCount, graphSize.width - margin.left - margin.right)
  const yScale = defineYScale(graviteCount, graphSize.height - margin.top - margin.bottom)
  const g = generateG(margin)

  // Draw the visual
  drawXAxis(g, xScale, graphSize.height - margin.top - margin.bottom)
  drawYAxis(g, yScale)
  drawBars(graviteCount, xScale, yScale, colorScale)
}

/**
 * Draws the button to toggle the display year.
 *
 * @returns {number} dimensions
 */
export function getDimensions () {
  const parentNode = d3.select('#PercentageChart').node().getBoundingClientRect()
  const containerDimension = {
    width: parentNode.width,
    height: parentNode.height
  }
  return containerDimension
}

/**
 * Description
 *
 * @param {number} graviteList The data to be used
 * @param {number} rowCount The array length
 * @returns {object[]} The grouped data
 */
export function getDistinctCount (graviteList, rowCount) {
  let graviteCount = graviteList.reduce((a, c) => (a[c] = (a[c] || 0) + 1, a), {})
  graviteCount = Object.keys(graviteCount).map(key => ({ GRAVITE: key, Count: graviteCount[key], Ratio: graviteCount[key] / rowCount }))
  graviteCount = graviteCount.sort((a, b) => a.Count - b.Count)
  return graviteCount
}

/**
 * Sets the domain and range of the X scale.
 *
 * @param {number} rowCount Category list The data to be used
 * @param {number} width The width of the graph
 * @returns {Element} The x axis scale
 */
export function defineXScale (rowCount, width) {
  return d3.scaleLinear()
    .domain([0, rowCount])
    .range([0, width])
}

/**
 * Sets the domain and range of the Y scale.
 *
 * @param {number} graviteList The data to be used
 * @param {number} heigth The width of the graph
 * @returns {Element} The y axis scale
 */
export function defineYScale (graviteList, heigth) {
  graviteList = graviteList.map(function (arr) { return arr.GRAVITE })
  return d3.scaleBand()
    // .padding(0.15)
    .domain(graviteList)
    .range([heigth, 0])
}

/**
 * Generates the SVG element g which will contain the data visualisation.
 *
 * @param {object} margin The desired margins around the graph
 * @returns {*} The d3 Selection for the created g element
 */
export function generateG (margin) {
  d3.selectAll('#graph-g').remove()
  d3.selectAll('.title').remove()
  d3.selectAll('.title2').remove()
  return d3.select('#PercentageChart')
    .append('g')
    .attr('id', 'graph-g')
    .attr('transform',
      'translate(' + margin.left + ',' + margin.top + ')')
}

/**
 * Draws the x axis at the bottom of the plot.
 *
 * @param {Element} g Base element
 * @param {*} xScale The scale to use for the x axis
 * @param {number} height The height of the graph
 */
export function drawXAxis (g, xScale, height) {
  g.append('g')
    .attr('class', 'x.axis')
    .attr('transform', 'translate(0, ' + height + ')')
    .call(d3.axisBottom(xScale).tickFormat(x => x.toLocaleString('en-US')).ticks(5).tickSize(-height))
}

/**
 * Draws the y axis at the left of the plot.
 *
 * @param {Element} g Base element
 * @param {*} yScale The scale to use for the y axis
 */
export function drawYAxis (g, yScale) {
  g.append('g')
    .attr('class', 'y.axis')
    .call(d3.axisLeft(yScale))
}

/**
 * Creates the groups for the grouped bar chart and appends them to the graph.
 * Each group corresponds to an act.
 *
 * @param {object[]} data The data to be used
 * @param {*} xScale The graph's x scale
 * @param {*} yScale The graph's y scale
 * @param {*} colorScale The graph'x color scale
 */
export function drawBars (data, xScale, yScale, colorScale) {
  let clickArray = []
  const tooltip = defineTooltip()

  // Title
  d3.select('#PercentageChart')
    .append('text')
    .text('Proportion des types d\'accidents')
    .attr('class', 'title')
    .style('font-size', '16px')
    .attr('transform', 'translate(15, 25)')
    .attr('dy', '0em')

  d3.select('#PercentageChart')
    .append('text')
    .text('Interagissez avec le graphique à l\'aide de votre souris en le survolant ou en cliquant sur plusieures barres')
    .attr('class', 'title2')
    .style('font-size', '10px')
    .attr('transform', 'translate(15, 25)')
    .attr('dy', '1.5em')
    .attr('opacity', 0.3)

  // Define group for bars
  const g = d3.select('#graph-g')
    .append('g')
    .attr('class', 'bar-groups')
    // .attr('id', d => `g-${d.GRAVITE}`)
    .selectAll('g')
    .data(data)
    .enter()
    .append('g')
    .attr('id', d => 'bar-g-' + d.GRAVITE)

  // Background Bars
  g.append('rect')
    .attr('class', 'bars')
    // .attr('id', function (d) { return d.GRAVITE })
    .attr('id', d => 'bar-negrect-' + d.GRAVITE)
    // .attr('width', d => xScale((d.Count / d.Ratio) - d.Count))
    .attr('width', d => xScale((d.Count / d.Ratio)))
    .attr('height', 50)
    // .attr('height', d => xScale(d.Count))
    // .attr('x', d => xScale(d.Count))
    .attr('x', 0)
    .attr('y', d => yScale(d.GRAVITE))
    .attr('fill', '#EDF3FA')
    .on('mouseover', function (event, d) { tooltip.style('display', 'block').text((d.Ratio * 100).toFixed(1) + '%') })
    .on('mousemove', function (event) { tooltip.style('left', (event.pageX + 10) + 'px').style('top', (event.pageY - 25) + 'px') })
    .on('mouseout', function () { tooltip.style('display', 'none').style('opacity', 1) })
    .on('click', function (d) {
      if (clickArray.length >= 2) {
        clickArray = resetBarClick()
      } else {
        clickArray = setBarClick(d, clickArray)
      }
      d3.select('#bar-g-arrow').on('click', function () { clickArray = resetBarClick() })
    })

  // Bars
  g.append('rect')
    .attr('class', 'bars')
    // .attr('id', function (d) { return d.GRAVITE })
    .attr('id', d => 'bar-rect-' + d.GRAVITE)
    .attr('width', d => xScale(d.Count))
    .attr('height', 50)
    // .attr('height', (d) => yScale(data[1].GRAVITE - data[0].GRAVITE))
    .attr('x', 0)
    .attr('y', d => yScale(d.GRAVITE))
    .attr('fill', d => colorScale(d.GRAVITE))
    .on('mouseover', function (event, d) { tooltip.style('display', 'block').text((d.Ratio * 100).toFixed(1) + '%') })
    .on('mousemove', function (event) { tooltip.style('left', (event.pageX + 10) + 'px').style('top', (event.pageY - 25) + 'px') })
    .on('mouseout', function () { tooltip.style('display', 'none').style('opacity', 1) })
    .on('click', function (d) {
      if (clickArray.length >= 2) {
        clickArray = resetBarClick()
      } else {
        clickArray = setBarClick(d, clickArray)
      }
      d3.select('#bar-g-arrow').on('click', function () { clickArray = resetBarClick() })
    })

  // Labels
  g.append('text')
    .attr('class', 'h2')
    .attr('id', d => 'bar-label-' + d.GRAVITE)
    .text(d => d.Count.toLocaleString('fr-CA'))
    .attr('x', d => xScale(d.Count))
    .attr('y', d => yScale(d.GRAVITE))
    .attr('transform', 'translate(5, ' + 50 / 2 + ')')
    .attr('fill', 'black')
    .attr('pointer-events', 'none')
}

/**
 * Draws the y axis at the left of the plot.
 *
 * @returns {*} The d3 Selection for the created g element
 */
export function resetBarClick () {
  const transitionTime = 200
  d3.select('#foreground-rect').transition().duration(transitionTime).attr('opacity', 0).remove()
  d3.select('#bar-g-arrow').attr('opacity', 1).transition().duration(transitionTime).attr('opacity', 0).remove()
  return []
}

/**
 * Draws the y axis at the left of the plot.
 *
 * @param {object[]} d Rectangles info
 * @param {object[]} clickArray Rectangles info
 * @returns {object[]} d Rectangles info
 */
export function setBarClick (d, clickArray) {
  // Keep data from selection
  const dataContainer = d.target.__data__
  const textLabel = d3.select(`text[id='bar-label-${dataContainer.GRAVITE}']`)

  // Populate rectangle array
  clickArray.push({
    Value: dataContainer.Count,
    Gravite: dataContainer.GRAVITE,
    Position: [textLabel.attr('x'), textLabel.attr('y')],
    Height: d3.select(`rect[id='bar-rect-${dataContainer.GRAVITE}']`).attr('height')
  })

  // Process the message
  if (clickArray.length === 2) {
    // Process the message to display
    const maxCount = Math.max(clickArray[0].Value, clickArray[1].Value)
    const minCount = Math.min(clickArray[0].Value, clickArray[1].Value)
    const diff = (maxCount / minCount).toFixed(1)
    const message = (clickArray[0].Value < clickArray[1].Value ? diff + 'x plus bas' : diff + 'x plus élevé')

    // Keep some variables for later on
    const maxX = Math.max(clickArray[0].Position[0], clickArray[1].Position[0])
    const minY = Math.min(clickArray[0].Position[1], clickArray[1].Position[1])
    const diffY = Math.abs(clickArray[0].Position[1] - clickArray[1].Position[1])
    const xTranslate = 55
    const transitionTime = 200

    // Create a group for the arrow+text
    const arrowGroup = d3.select('.bar-groups')
      .append('g')
      .attr('id', 'bar-g-arrow')

    // Add Background/Foreground Rectangle to display between the chart and the arrow
    d3.select('#bar-g-arrow')
      .append('rect')
      .attr('id', 'foreground-rect')
      .attr('width', d3.select('.bar-groups').node().getBoundingClientRect().width * 1.1)
      .attr('height', d3.select('.bar-groups').node().getBoundingClientRect().height * 1.1)
      .attr('fill', 'white')
      .attr('transform', 'translate(-1, -10)')
      .transition().duration(transitionTime).attr('opacity', 0.6)

    // Arrow Path
    const curve = d3.line().curve(d3.curveBumpX)
    const midPoint = [maxX + (diffY / 2), minY + (diffY / 2)]
    const points = [clickArray[0].Position, midPoint, clickArray[1].Position]
    arrowGroup.append('path')
      .attr('id', 'arrowPath')
      .attr('d', curve(points))
      .attr('transform', `translate(${xTranslate},${20})`)
      .attr('fill', 'transparent')
      .attr('stroke', 'black')
      .transition().duration(transitionTime).attr('opacity', 1)

    // Arrow Head
    arrowGroup.append('text')
      .text('◄')
      .attr('font-size', 12)
      .attr('x', clickArray[1].Position[0])
      .attr('y', clickArray[1].Position[1])
      .attr('text-anchor', 'left')
      .attr('transform', `translate(${xTranslate - 9}, 25)`)
      .attr('fill', 'black')
      .transition().duration(transitionTime).attr('opacity', 1)

    // Arrow Text
    arrowGroup.append('text')
      .text(message)
      .attr('x', midPoint[0])
      .attr('y', midPoint[1])
      .attr('text-anchor', 'left')
      .attr('transform', `translate(${xTranslate + 5}, ${20 + 5})`)
      .attr('fill', 'black')
      .transition().duration(transitionTime).attr('opacity', 1)
  }
  return clickArray
}

/**
 * Draws the y axis at the left of the plot.
 *
 * @returns {*} The d3 Selection for the created g element
 */
export function defineTooltip () {
  d3.selectAll('.tooltip').remove()
  return d3.select('body')
    .append('div')
    .attr('class', 'tooltip')
    .style('position', 'absolute')
    .style('display', 'none')
    .style('pointer-events', 'none')
    .style('background', 'rgba(0, 0, 0, 0.7)')
    .style('color', 'white')
    .style('padding', '10px')
    .style('border-radius', '5px')
}
